/* eslint-disable import/imports-first */
import React, { useState, useEffect } from 'react';

import styled from 'styled-components';
import { motion, useAnimation } from "framer-motion"

// image
import person from "@@packages/assets/images/onboarding/pose.png";
import which1 from "@@packages/assets/images/onboarding/which1.png";
import which2 from "@@packages/assets/images/onboarding/which2.png";
import which3 from "@@packages/assets/images/onboarding/which3.png";
import which4 from "@@packages/assets/images/onboarding/which4.png";
import which5 from "@@packages/assets/images/onboarding/which5.png";

import {Animated} from "react-animated-css";
import OtherDrag from './OtherDrag';

const transition = (duration = 5, delay = 0, repeatDelay = 0) =>  {
  return {
    duration: duration,
    repeat: Infinity,
    delay: delay,
    repeatDelay: repeatDelay,
  };
};


function Person({ isBounceRight}) {
  const controls1 = useAnimation();
  const controls2 = useAnimation();
  const controls3 = useAnimation();
  const controls4 = useAnimation();
  const controls5 = useAnimation();
  const [distance, setDistance] = useState(0);
  const [animate] = useState(() => isBounceRight? "bounceInRight": "bounceInLeft");
  const [delay] = useState(() => isBounceRight? [0, 700, 1000, 1300, 1600, 1800]: [1800, 1600, 1300, 1000, 700,0]);
  useEffect(() => {
    const a = setTimeout(() => {
      setDistance(0);
    },300);
    return () => clearTimeout(a);
  },[distance]);

  useEffect(() => {
      controls1.start({
        top: [-30, -50, -30],
        transition: transition(),
      });
      controls2.start({
        scale: [1, 0.5, 1],
        transition: transition(6),
      });
      controls4.start({
        top: [200, 150, 200],
        transition: transition(4),
      });
      controls3.start({
        x: [-50, 0, -50],
        transition: transition(4),
      });
      controls5.start({
        x: [-50, 0, -50],
        transition: transition(4),
      });
  }, []);

  return (
    <>
    <OtherDrag
      setDistance={setDistance}
    >
    <Wrapper className="wrapContent">
        <div className="wrapPerson">
          <Animated className="person" animationIn={animate} animationInDelay={delay[1]}>
            <img
              src={person}
              alt="Person"
              style={{right: 0 - distance/2}}
            />
          </Animated>
          <Animated className="wrapAnimate" style={{zIndex: 15}} animationIn={animate} animationInDelay={delay[0]}>
            <motion.img
              animate={controls1}
              className="which1" src={which1}
              style={{right: -180 - distance }}
            />
          </Animated>
          <Animated className="wrapAnimate" animationIn={animate} animationInDelay={delay[2]}>
            <motion.img
              animate={controls2}
              className="which2"
              src={which2}
              style={{right: -150 - distance }}

            />
          </Animated>
          <Animated className="wrapAnimate" style={{zIndex: 15 }} animationIn={animate} animationInDelay ={delay[3]}>
            <motion.img
              animate={controls3}
              className="which3"
              src={which3}
              style={{left: -30 + distance}}
            />
          </Animated>
          <Animated className="wrapAnimate" animationIn={animate} animationInDelay ={delay[4]}>
            <motion.img
              animate={controls4}
              className="which4"
              src={which4}
              style={{left: -200 + distance}}
            />
          </Animated>
          <Animated className="wrapAnimate" animationIn={animate} animationInDelay ={delay[5]}>
            <motion.img
              animate={controls5}
              className="which5"
              src={which5}
              style={{left: -180  + distance}}
            />
          </Animated>
        </div>
    </Wrapper>
    </OtherDrag>
    </>
  );
}

export default Person;

const Wrapper = styled.div`
  position: relative;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  top: 10px;
  height: 100%;
  .wrapPerson {
    width: 310px;
    height: 394px;
    position: relative;
    display: flex;
    justify-content: center;
  }
  img, .wrapAnimate {
    position: absolute;
  }
  .person {
    z-index: 10;
   img {
    position: relative;
    transition: all 0.5s linear;
   }
  }
  .which1 {
    z-index: 8;
    transition: all 0.5s linear;
  }
  .which2 {
    z-index: 15;
    transition: all 0.5s linear;
    top: 160px;
  }
  .which3 {
    transition: all 0.5s linear;
    top: 220px;
  }
  .which4 {
    z-index: 8;
    transition: all 0.5s linear;
  }
  .which5 {
    z-index: 8;
    transition: all 0.5s linear;
  }
`;

