import React, { memo, useEffect, useState } from 'react';
import { motion } from "framer-motion";

const OtherDrag = (({setDistance, children}) => {
  const [runTime, setRunTime] = useState(false);
  useEffect(() => {
    let a;
    if(!runTime){
     a = setTimeout(() => {
        setRunTime(true);
      },100);
    }
    return () => a && clearTimeout(a);
  },[runTime]);
  const HandleOnPan  = (e, info) => {
    const distance =  info.offset.x;
    if(runTime){
      setRunTime(false);
    }
    setDistance(distance);
  };
  return (
    <motion.div
      onPan={HandleOnPan}
      style={{touchAction: "none", height: "100%"}}
    >
      {children}
    </motion.div>
  );
});

export default memo(OtherDrag);
