import React, { useEffect, useState } from 'react';
import { SystemCore } from '@@packages/core';
import styled from 'styled-components';
import ContentText from './components/ContentText';
import Person from './components/Person';
import Bg from '@@packages/assets/images/bg.png';

const App = () => {
  const [isReady, setIsReady] = useState(SystemCore.isReady);

  const _handleSystemReady = () => {
    setIsReady(SystemCore.isReady);
  };

  useEffect(() => {
    if (!SystemCore.isReady) {
      SystemCore.on('ready', _handleSystemReady);
      return;
    }

    return () => {
      SystemCore.removeEventListener('ready', _handleSystemReady);
    };
  }, []);

  useEffect(() => {
    console.log(isReady, 'core is ready ????');
    if (isReady) {
    }
  }, [isReady]);

  return (
    <Container>
      <WrapSwiper
        style={{
          backgroundImage: `url(${Bg})`,
          backgroundSize: '100% 100%',
        }}>
        <Person isBounceRight={false} />
        <ContentText />
      </WrapSwiper>
    </Container>
  );
};

export default App;

export const Container = styled.div`
  display: flex;
  width: 100%;
  height: calc(100vh - 20px);
  flex-direction: column;
  box-sizing: border-box;
  background: #fdfdfd;
  position: relative;
  overflow: hidden;
`;
const WrapSwiper = styled.div`
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 85%;
  background-repeat: no-repeat, repeat;
  background-size: 100% 100%;
  .wrapContent {
    @media (max-height: 750px) {
      transform: scale(0.9);
    }
    @media (max-height: 710px) {
      transform: scale(0.8);
    }
  }
`;
